<template>
  <div>
    <section class="section has-background-white-ter px-1 py-2">
      <div class="container">
        <div v-for="group in Object.keys(getMenuItemsGroupped)" :key="group">
          <h2 class="title my-2">{{ group }}</h2>
          <div class="columns is-multiline">
            <div
              class="column is-half-tablet is-one-third-desktop"
              v-for="item in getMenuItemsGroupped[group]"
              :key="item.id"
            >
              <MenuItemImg
                :item="item"
                @itemadd="changeOrderItems({ id: item.id, qty: 1 })"
                @itemremove="changeOrderItems({ id: item.id, qty: -1 })"
                @itemdetails="modalToggle(item.id)"
              />
              <MenuModal
                :item="item"
                :modal="modal"
                @itemadd="changeOrderItems({ id: item.id, qty: 1 })"
                @itemremove="changeOrderItems({ id: item.id, qty: -1 })"
                @close="modalToggle(null)"
              />
            </div>
          </div>
        </div>
      </div>
    </section>
    <Footer />
  </div>
</template>

<script>
import { mapMutations, mapGetters } from "vuex";

import MenuItemImg from "@/components/MenuItemImg";
import MenuModal from "@/components/MenuModal";
import Footer from "@/components/Footer";

export default {
  name: "Menu",
  components: {
    MenuItemImg,
    MenuModal,
    Footer,
  },
  data() {
    return {
      modal: {
        id: null,
      },
    };
  },

  computed: {
    ...mapGetters(["getMenuItemsGroupped"]),
  },
  methods: {
    ...mapMutations(["changeOrderItems"]),

    modalToggle(id) {
      this.modal.id = id;
    },
  },
};
</script>
